import React, { Component } from 'react'
import Link from 'next/link'
import styled from 'styled-components'
import uuid from "uuid/v4";

 const Outerbox = styled.div`
  width: 80%;
  margin-left: 50%;
  transform: translateX(-50%);
 `;
  const InnerBox = styled.div`
  `;
  const NavItem = styled.div`
  background: purple;
  display: grid;
  margin-left: 50%;
  transform: translateX(-50%);
  grid-template-columns: repeat(7, 1fr);
  grid-gap: 5px;
  min-width: 800px;
  text-align: center;
  background: #000;
  justify-content: space-around;
    a {
      color: white;
    }
  `;
 const Title = styled.div`
  background: green;
  border: 1px solid red;
  margin-top: 30px;
  width: 100%; height: 40px;
 `;
 
class links extends Component {
  constructor(props) {
    super(props)
    this.state = {
      navItems: [
        { id: uuid(), name: "Feed",},
        { id: uuid(), name: "Recognition" },
        { id: uuid(), name: "Actions" },
        { id: uuid(), name: "Members" },
        { id: uuid(), name: "Mail" },
        { id: uuid(), name: "Performance" },
        { id: uuid(), name: "Settings" }
      ]
    }
  }
  renderNavItems() {
    return(
      <NavItem>
        {this.state.navItems.map(item => (
          <Link href={`/${item.link}`}>
            <a>
            <InnerBox key={item.id}>
              <Title>{item.name}</Title>
            </InnerBox>
            </a>
          </Link>
        ))}
      </NavItem>
    )
  }
  render() {
    return (
      <Outerbox>
        {this.renderNavItems()}
      </Outerbox>
     )
   }
 }

export default links
